<template>
  <div id="app">
    <hr>
    <z-fieldset title="fieldset测试">批量注册fieldset测试（传了一个title）</z-fieldset>
    <hr>
    <z-button>没有传参批量注册button按钮组件</z-button>
    <hr>
    <h1>千锋基于VUE的UI组件库</h1>
    <z-row>
        <z-button>默认按钮</z-button>
        <z-button type="primary">主要按钮</z-button>
        <z-button type="success">成功按钮</z-button>
        <z-button type="info">信息按钮</z-button>
        <z-button type="warning">警告按钮</z-button>
        <z-button type="danger">危险按钮</z-button>
    </z-row>

    <z-row>
        <z-button round>默认按钮</z-button>
        <z-button type="primary" round>主要按钮</z-button>
        <z-button type="success" round>成功按钮</z-button>
        <z-button type="info" round>信息按钮</z-button>
        <z-button type="warning" round>警告按钮</z-button>
        <z-button type="danger" round>危险按钮</z-button>
    </z-row>


<z-fieldset>
  <h1>神龙教主</h1>
</z-fieldset>

<z-fieldset title="神龙教主">
  <h1>洪福齐天，寿与天齐</h1>
</z-fieldset>


<z-table :column="column" :data="data"></z-table>

<z-row>
  <z-page :total="10" @changePage="changePageFn"></z-page>
</z-row>

  </div>
</template>

<script>
export default {
  data() {
    return {
      
        column: [
          {title: '编号', key: 'id'},
          {title: '姓名', key: 'name', render(row, data) { return `<b>${data}</b>`}},
          {title: '年龄', key: 'age'},
          {title: '性别', key: 'sex', render(row, data) { return data==1? '男' : '女'}},
        ],
        data: [
          {id:1, name: 'zuo', age: 18, sex: 1},
          {id:2, name: 'zfl', age: 18, sex: 2},
          {id:3, name: 'zflone', age: 18, sex: 1},
          {id:4, name: 'zuofeilone', age: 18, sex: 2},
        ]
    }
  },
  methods: {
    changePageFn(n) {
      alert(n)
    }
  }
}
</script>

