<template>
<div class="todolist">
  <NavBar title="备忘录" @click-back="backFn"/>
  <h1>todolist</h1>
  <input type="text" v-model="inputstr">
  <button @click="addFn">添加</button>
  <hr>
  <ul>
    <li v-for="(item,index) in todos" v-bind:key="index">{{item.title}}
      <button @click="delFn(index)">删除</button>
    </li>
  </ul>
  <!-- <ul>
    <li>fdsfd</li>
    <li>dfsf</li>
    <li>dfsdf</li>
  </ul> -->
</div>
</template>
<script>
// import NavBar from '@/components/NavBar'

export default {
   data () {
     return {
       inputstr:'',
       todos:[
         {id:1,title:"吃饭"},
         {id:2,title:"睡觉"},
         {id:3,title:"打豆豆"}
       ]
     }
   },
   methods: {
     backFn(){
        alert('返回')
     },
     addFn() {
       this.todos.push({
         id:this.todos.length+1,
         title:this.inputstr,
       })
     },
     delFn(index){
       this.todos.splice(index,1)
     }
   },
   components: {
      // NavBar,
   }
 }
</script>
<style scoped>
 
</style>
